/* esline-disable */
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

  let 박스 = styled.div`
    padding : 20px;
  `;
  let 제목 = styled.h4`
    font-size : 25px;
    color : ${ props => props.색상 }
  `; 

function Detail(props) {

  let [alert, alert변경] = useState(true); // amount alert 표시 여부 State
  let [input, input변경] = useState('');

  useEffect(()=>{

    let 타이머 = setTimeout(()=>{ alert변경(false) }, 2000);
    console.log("안녕");
    return ()=>{ clearTimeout(타이머) }
  },[alert]);
  //   ▲ alert 라는 state가 변경이 될때만 실행! (여러개 입력 가능)

  let { id } = useParams();
  let history = useHistory();
  let 찾은상품 = props.shoes.find(x => x.id == id);

  return (
    <div className="container">
      <박스>
        <제목 className="red" >Detail</제목>
      </박스>

      <input onChange={(e)=>{ input변경(e.target.value) }}/>
      <br/>

      {
        alert === true
        ? <div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
        : null
      }

      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          {/* <img src={ 'https://codingapple1.github.io/shop/shoes' + props.shoes.id + '.jpg' } width="100%" /> */}
        </div>

        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}</p>

          <Info 재고={props.재고}></Info>

          <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12]) }}>주문하기</button>
          &nbsp;
          <button className="btn btn-danger" onClick={()=>{
            history.push('/')
          }}>뒤로가기</button>
        </div>
      </div>
    </div>
  )
  }

  function Info(props) {
    return (
      <p>재고 : {props.재고[0]}</p>
    )
  }

  export default Detail;